                    commentary on the fragility and resilience of life. <br>

                    Nicola Hicks's sculptural piece was arresting - a green vase adorned with etched figures, topped with a
                    wild, almost untamed figure. It was as if mythology had spilled into reality, the vase becoming a vessel
                    of stories, each etching a narrative thread woven into the physicality of the object. <br>

                    Then, there was "Body Armour (with chain)" by Maria Positano, which contrasted vulnerability with
