                    can impose. Its counterpart, "Body Armour (with fabric)," draped in fabric, suggested a softer, more
                    personal form of defense, perhaps against the emotional battles we face. <br>

                    Henry Hussey's "Unique Sculpture" was a stark, polished black piece that invoked the austerity of a
                    helmet, an object carrying both the weight of its protective purpose and the burden of the stories of
                    those who wore it. <br>

                    The exhibition space itself became part of the narrative. Each room was an immersive environment where
