xperience. It was an acknowledgment of the shared but solitary nature of heartache. Engaging with the art in such a tactile manner bridged the gap between the artist's intent and my personal reflection, allowing for a moment of solidarity in the otherwise isolated experience of pain."),_c('br'),_vm._v(" The exhibition was not only a showcase of artistic talent but also a sanctuary for emotional exploration. Elliott's work, blurring the lines between sculpture and sanctuary, art and therapy, extended an invitation to confront and perhaps comfort the often neglected injuries of the heart. It was a reminder that in the aftermath of emotional rupture, there is potential for a new form of beauty, one that honors the past while forging a path forward."),_c('br'),_vm._v(" 'Cry Me a River' was more than an exhibition; it was a narrative of rupture and reconciliation. It was a testament to the transformative power of art to embody, evoke, and alleviate the human condition. Elliott's work, echoing the fragility and fortitude of the heart, left an indelible impression on me, one of appreciation for the complex tapestry of the human emotional experience."),_c('br')])]),_c('div',{staticClass:"rigth"},[_c('br'),_c('ul',[_c('li',[_c('img',{attrs:{"src":require("../../assets/img/11.12/image001.jpg"),"alt":""}})]),_c('li',[_c('img',{attrs:{"src":require("../../assets/img/11.12/image003.jpg"),"alt":""}})]),_c('li',[_c('img',{attrs:{"src":require("../../assets/img/11.12/image005.jpg"),"alt":""}})]),_c('li',[_c('img',{attrs:{"src":require("../../assets/img/11.12/image007.jpg"),"alt":""}})]),_c('li',[_c('img',{attrs:{"src":require("../../assets/img/11.12/image009.jpg"),"alt":""}})]),_c('li',[_c('img',{attrs:{"src":require("../../assets/img/11.12/image011.jpg"),"alt":""}})]),_c('li',[_c('img',{attrs:{"src":require("../../assets/img/11.12/image013.jpg"),"alt":""}})]),_c('li',[_c('img',{attrs:{"src":require("../../assets/img/11.12/image015.jpg"),"alt":""}})]),_c('li',[_c('img',{attrs:{"src